  function kodddd(dddd) {
    switch (dddd) {
      case '월요일':
        dddd = '월';
        break;
      case '화요일':
        dddd = '화';
        break;
      case '수요일':
        dddd = '수';
        break;
      case '목요일':
        dddd = '목';
        break;
      case '금요일':
        dddd = '금';
        break;
      case '토요일':
        dddd = '토';
        break;
      case '일요일':
        dddd = '일';
        break;
    }
    return dddd;
  }

  function thisMonday() {
    let today = moment();
    let monday;
    switch (today.format('dddd')) {
      case '월요일':
        monday = moment();
        break;
      case '화요일':
        monday = moment().subtract(1, 'days');
        break;
      case '수요일':
        monday = moment().subtract(2, 'days');
        break;
      case '목요일':
        monday = moment().subtract(3, 'days');
        break;
      case '금요일':
        monday = moment().subtract(4, 'days');
        break;
      case '토요일':
        monday = moment().subtract(5, 'days');
        break;
      case '일요일':
        monday = moment().subtract(6, 'days');
        break;
    }
    return monday
  }

  function thisWeek() {
    let thisWeekDddd = [];
    let thisWeekDays = [];
    let next1WeekDays = [];
    let next2WeekDays = [];
    let last1WeekDays = [];

    for (let i = 0; i < 7; i++) {
      thisWeekDddd.push(kodddd(thisMonday().add(i, 'days').format('dddd')));
      thisWeekDays.push(kodddd(thisMonday().add(i, 'days').format('D')));
      next1WeekDays.push(kodddd(thisMonday().add(i + 7, 'days').format('D')));
      next2WeekDays.push(kodddd(thisMonday().add(i + 14, 'days').format('D')));
      last1WeekDays.unshift(kodddd(thisMonday().subtract(i + 1, 'days').format('D')));
    }

    let thisWeek = { dddd: thisWeekDddd, days: [last1WeekDays, thisWeekDays, next1WeekDays, next2WeekDays] };

    // console.log(thisWeek);
    // console.log(moment().format('D'));
    return thisWeek;
  }

